.PageContentCard {
  border: 1px solid var(--tableGridColor);
  border-radius: var(--borderRadius);
  max-width: 280px;
  width: 100%;
}

.PageContentCard .p-card-content {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  align-items: center;
}

.PageContentCard_icon {
  width: 40px;
  height: 40px;
  font-size: 40px !important;
}

.PageContentCard_title{
  font-size: 2em;
}